import Vue from 'vue'
import VueRouter from 'vue-router'
import Index from './views/Index'
import Login from './views/Login'
import Sign from './views/Sign'
import Forget from './views/Forget'
import Protocol from './views/Protocol'
import Case from './views/Case'
import SafeRights from './views/SafeRights'
import Retransmission from './views/Retransmission'
import Article from './views/Article'
import Verify from './views/Verify'
import Compromise from './views/Compromise'
import User from './views/User'
import Auth from './views/Auth'
import AuthPerson from './views/AuthPerson'
import AuthCompany from './views/AuthCompany'
import AuthWaiting from './views/AuthWaiting'
import Recharge from './views/Recharge'
import Withdraw from './views/Withdraw'

Vue.use(VueRouter)

let router = new VueRouter({
    // mode: 'history',
    routes: [
        { 
            path: '/',
            redirect: '/index'
        },
        {
            path: '/index',
            component: Index,
            name: 'index',
            meta: {
                title: '首页'
            }
        },
        {
            path: '/login',
            component: Login,
            name: 'login',
            meta: {
                title: '登录'
            }
        },
        {
            path: '/sign',
            component: Sign,
            name: 'sign',
            meta: {
                title: '注册'
            }
        },
        {
            path: '/forget',
            component: Forget,
            name: 'forget',
            meta: {
                title: '注册'
            }
        },
        {
            path: '/protocol',
            component: Protocol,
            name: 'protocol',
            meta: {
                title: '用户使用协议'
            }
        },
        {
            path: '/case',
            component: Case,
            name: 'case',
            meta: {
                title: '成功判例'
            }
        },
        {
            path: '/safeRights',
            component: SafeRights,
            name: 'safeRights',
            meta: {
                title: '我要维权'
            }
        },
        {
            path: '/retransmission',
            component: Retransmission,
            name: 'retransmission',
            meta: {
                title: '我要转载'
            }
        },
        {
            path: '/article',
            component: Article,
            name: 'article',
            porps: true,
            meta: {
                title: '我要转载文章详情'
            }
        },
        {
            path: '/verify',
            component: Verify,
            name: 'verify',
            meta: {
                title: '验证查询'
            }
        },
        {
            path: '/compromise',
            component: Compromise,
            name: 'compromise',
            meta: {
                title: '我要和解'
            }
        },
        {
            path: '/user/:userId',
            component: User,
            name: 'user',
            meta: {
                title: '个人中心'  //账户状态 是否认证 是否绑定公众号
            },
            children: [
                {
                    path: '/auth',
                    component: Auth,
                    name: 'auth',
                    meta: {
                        title: '实名认证'
                    },
                    children: [
                        {
                            path: '/person',
                            component: AuthPerson,
                            name: 'authPerson',
                            meta: {
                                title: '个人认证'
                            },
                        },
                        {
                            path: '/company',
                            component: AuthCompany,
                            name: 'authCompany',
                            meta: {
                                title: '企业认证'
                            },
                        },
                        {
                            path: '/waiting',
                            component: AuthWaiting,
                            name: 'authWaiting',
                            meta: {
                                title: '认证审核中'
                            },
                        },
                    ]
                },
                {
                    path: '/recharge',
                    component: Recharge,
                    name: 'recharge',
                    meta: {
                        title: '认证充值' || '积分充值'
                    },
                },
                {
                    path: '/withdraw',
                    component: Withdraw,
                    name: 'withdraw',
                    meta: {
                        title: '提现'
                    },
                },
                {
                    path: '/a',
                    component: Retransmission,
                    name: 'a',
                    meta: {
                        title: '微信公众号确权'
                    }
                },
                {
                    path: '/b',
                    component: Retransmission,
                    name: 'b',
                    meta: {
                        title: '原创作品确权'
                    }
                },
                {
                    path: '/c',
                    component: Retransmission,
                    name: 'c',
                    meta: {
                        title: '侵权监控'
                    }
                },
                {
                    path: '/d',
                    component: Retransmission,
                    name: 'd',
                    meta: {
                        title: '侵权存证'
                    }
                },
                {
                    path: '/e',
                    component: Retransmission,
                    name: 'e',
                    meta: {
                        title: '发起维权'
                    }
                },
                {
                    path: '/f',
                    component: Retransmission,
                    name: 'f',
                    meta: {
                        title: '我的授权'
                    }
                },

                {
                    path: '/g',
                    component: Retransmission,
                    name: 'g',
                    meta: {
                        title: '授权记录'
                    }
                },
                {
                    path: '/h',
                    component: Retransmission,
                    name: 'h',
                    meta: {
                        title: '我的购买'
                    }
                },
                {
                    path: '/i',
                    component: Retransmission,
                    name: 'i',
                    meta: {
                        title: '我要调解'
                    }
                },
                {
                    path: '/j',
                    component: Retransmission,
                    name: 'j',
                    meta: {
                        title: '调解记录'
                    }
                },

            ]
        },
        
        
    ]
})

