<template>
  <div>
    <UserLayout>
    <template slot="main">
      <div class="tabs">
        <a class="tab" :class="{'active': activeTab==='#profile'}" href="#profile">账号信息</a>
        <a class="tab" :class="{'active': activeTab==='#auth'}" href="#auth">实名信息</a>
        <a class="tab" :class="{'active': activeTab==='#record'}" href="#record">消费记录</a>
      </div>
      <div class="contents">
        <div class="content" v-if="activeTab==='#profile'">
          <el-card class="card1" header="账号信息" body-style="padding: 0 49px;">
            <div class="row1">
              <el-upload
                class="avatar-uploader"
                action="https://jsonplaceholder.typicode.com/posts/"
                :show-file-list="false"
                :on-success="handleAvatarSuccess"
                :before-upload="beforeAvatarUpload">
                <img :src="user.avatarUrl" class="avatar"/>
                <i class="el-icon-camera" :class="{'center': !user.avatarUrl, 'bottom': user.avatarUrl}"></i>
              </el-upload>
              <span class="nick">{{user.nickname}}</span>
            </div>
            <el-row :gutter="60">
              <el-col :span="12">
                <div class="label">昵称</div>
                <input type="text" placeholder="请输入昵称" v-model="user.nickname" disabled/>
                <i class="el-icon-edit"></i>
              </el-col>
              <el-col :span="12">
                <div class="label">绑定电话</div>
                <input type="text" placeholder="请输入绑定电话" v-model="user.phone" disabled/>
                <i class="el-icon-edit"></i>
              </el-col>
              <el-col :span="12">
                <div class="label">账号密码</div>
                <input type="password" placeholder="请输入密码" v-model="user.password" disabled/>
                <i class="el-icon-edit"></i>
              </el-col>
            </el-row>

          </el-card>
          <el-card class="card2" header="提现账户">
            <el-row>
              <el-col :span="12">
                <div class="label">支付宝</div>
                <input type="text" placeholder="" v-model="user.alipay" disabled/>
                <i class="el-icon-connection"></i>
              </el-col>
              <el-col :span="12">
                <div class="label">微信</div>
                <input type="text" placeholder="" v-model="user.wepay" disabled/>
                <i class="el-icon-connection"></i>
              </el-col>
            </el-row>
          </el-card>
        </div>
        <div class="content" v-if="activeTab==='#auth'">
          <el-card class="card3" header="认证信息"></el-card>
        </div>
        <div class="content" v-if="activeTab==='#record'">
          <el-card class="card3">
            <div slot="header" class="clearfix">
              
            </div>
            <div v-for="o in 4" :key="o" class="text item">
              {{'列表内容 ' + o }}
            </div>
        </el-card>
        </div>
      </div>
    </template>
    </UserLayout>
  </div>
</template>

<script>
import UserLayout from '../components/UserLayout'
export default {
  components: {UserLayout},
  data(){
    return{
      user: {
        avatarUrl: 'http://up.enterdesk.com/edpic/1b/93/f6/1b93f66f09f63a619f7f9f4146dedf84.jpg',
        nickname: '阔波特得星星',
        password: 1234566
      }
    }
  },
  computed:{
    activeTab(){
      console.log(this.$route.hash)
      return this.$route.hash || '#profile'
    },
  },
  methods:{
    // setTab(hash){
    //   this.$route.hash = hash;
    // }
  }
}
</script>

<style scoped>
.tabs{
  height: 66px;
  margin-top: 40px;
  overflow: hidden;
}
.tab{
  float: left;
  display: block;
  width: 190px;
  height: 66px;
  margin-right: 1px;
  text-align: center;
  line-height: 66px;
  font-size: 14px;
  color: #181C2F;
  background:rgba(163,169,182,0.36);
}
.tab:first-child{
  border-radius:6px 0px 0px 0px;
}
.tab.active{
  color: #fff;
  background: #066AFF;
}
.el-card{
  border: none;
  border-radius:0px 6px 6px 6px;
}
.card2{
  margin-top: 24px;
}
.row1{
  margin-top: 54px;
  margin-bottom: 44px;
}
.avatar-uploader{
  position:relative;
  display: inline-block;
  vertical-align: top;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background:rgba(163, 169, 182, 0.2);
}
.avatar-uploader .avatar{
  display: block;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  background:rgba(163, 169, 182, 0.2);
  overflow:hidden;
}
.el-icon-camera{
  position: absolute;
  color: #3639A4;
}
.el-icon-camera.bottom{
  bottom: -10px;
  right: 0;
  width: 24px;
  height: 24px;
  text-align: center;
  line-height: 24px;
  font-size: 12px;
  border-radius: 50%;
  background:#fff;
  box-shadow:0px 5px 8px rgba(0,0,0,0.02);
}
.nick{
  margin-left: 10px;
  font-size: 20px;
  line-height: 80px;
}
.label{
  height:24px;
  line-height:24px;
  font-size:18px;
  margin-bottom: 20px;
}
.el-col{
  padding-bottom: 30px;
}
.el-col input{
  width: 80%;
  height:52px;
  padding: 0 23px;
  background:rgba(255,255,255,1);
  border:1px solid rgba(163,169,182,0.2);
  opacity:1;
  border-radius:6px;
  box-sizing: border-box;
}
input:disabled{
  color: #181C2F;
}
.el-col i{
  vertical-align: top;
  margin-left: 20px;
  line-height: 52px;
  font-size: 32px;
  color: #066AFF;
}
</style>
